<template>
  <div class="tag-wrapper">
    <yd-navbar title="Student details">
        <router-link to="/interns" slot="left">
            <yd-navbar-back-icon>Back</yd-navbar-back-icon>
        </router-link>
    </yd-navbar>

    <div class="tags"> 

        <div class="player">
      <!-- <video-player  class="video-player vjs-custom-skin"
                     ref="videoPlayer"
                    >
      </video-player> -->
    </div>


        <yd-flexbox v-for="item, key in list" :key="key" class="item">
            <yd-flexbox-item>
                <div class="desc-wrapper">
                <div class="title"> 
                    <router-link to="/tag/1/">
                    <yd-button  bgcolor="#efefef" class="tagname">{{item.tagname}}</yd-button>
                    </router-link>
                    <p> From: {{item.from}}</p>
                    <span>Score:</span> 
                <el-rate
                v-model="4.6"
                disabled
                show-score
                text-color="#ff9900"
                score-template="{value}">
                </el-rate>

                    <p>Description: {{item.desc}}</p>
                
                    <span class="price"> Duration: {{item.duration}} months</span>
                </div>

            <div class="desc"> 
                <div class="action">
                    

                      <el-button type="success" plain @click="openConfrim">Hire this guy</el-button>

                </div>
            </div>
            </div>
            </yd-flexbox-item>
             <!-- <div class="profile-wrapper">
                <img  :src="item.owner_profile">
                <yd-button type="hollow" class="tagname">{{item.username}}</yd-button>
            </div> -->
        </yd-flexbox>

<h2 class="bigtitle">My skills</h2>
<yd-flexbox>
 
            <yd-flexbox-item><skills></skills></yd-flexbox-item>
</yd-flexbox>


 <div class="review-item" style="background:#fff; margin-top:30px;">
       <yd-flexbox class="review master-chela">
            <div class="profile-wrapper">
                    <img  src="https://img.huxiucdn.com/auth/data/avatar/000/04/36/70_1481772895.jpg?imageView2/1/w/486/h/486/|imageMogr2/strip/interlace/1/quality/85/format/jpg">
                    <a style="display:block;">Cool mentor</a> 
            </div>
            <yd-flexbox-item class="review-text">oh,I like this intern, the best I had before...stronglly recommended..</yd-flexbox-item>
        </yd-flexbox>

        <yd-flexbox class="review chela-master">
             <div class="profile-wrapper">
                    <img  src="https://img.huxiucdn.com/auth/data/avatar/000/04/36/70_1481772895.jpg?imageView2/1/w/486/h/486/|imageMogr2/strip/interlace/1/quality/85/format/jpg">
                     <a style="display:block;">Mad mentor</a>
            </div>
            
            <yd-flexbox-item class="review-text">This student is so lazy..I wouldn't recommend him again...</yd-flexbox-item>
           
        </yd-flexbox>

        </div>
        
    </div>
  </div>
</template>
<script>
import Vue from 'vue'
require('video.js/dist/video-js.css')
require('vue-video-player/src/custom-theme.css')
import VideoPlayer from 'vue-video-player'
Vue.use(VideoPlayer)
import skills from '@/page/skills/skills'

export default {
    methods:{
        openConfrim() {
                this.$dialog.confirm({
                    title: 'Confirm',
                    mes: 'Are you sure wanna hire this guy?',
                    opts: () => {
                        this.$dialog.toast({mes: '你点了确定', timeout: 1000});
                    }
                });
            },
    },
    components:{
        skills
    },
   
  data(){
      return {
            
        list:[
            {
                tagname:'Jane theman',
                from:'Singapore',
                desc:'I m passionate about any type of work and job..I m so good ,I m so awesome,hire me please!',
            
                duration:7,
                img:'https://img.huxiucdn.com/auth/data/avatar/000/04/36/70_1481772895.jpg?imageView2/1/w/486/h/486/|imageMogr2/strip/interlace/1/quality/85/format/jpg'
            },
           
        ]
      }

  }
}
</script>
<style lang="stylus" scoped>
.tag-wrapper
    .chela-master
        margin-top 20px
        margin-bottom 20px
    .tags
        .item
            background #ffffff
            border-bottom 1px solid #efefef
            padding 10px
        width 100%
        display inline-block
        .profile-wrapper

            display inline-block
            position flex
            max-width 100px
            img
                width 50%
                border-radius 50%

        .desc-wrapper
            .yd-badge-hollow
                margin-right 10px
            display inline-block
            position flex
            width 100%
            padding 10px
            .title
                text-align left 
            .desc
                text-align left
                padding 10px 0
                .action
                    margin-top 10px
</style>



